@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';


html,
body {
	width: 100%;
	height: var(--reVh, 1vh);
	height: calc(constant(safe-area-inset-bottom) + constant(safe-area-inset-top) + var(--reVh, 1vh) );
	height: calc(env(safe-area-inset-bottom) + env(safe-area-inset-top) + var(--reVh, 1vh) );
}

body {
	position: relative;
	background: #fff;
}

.wrapper{
	.container_wrapper{
		padding-top: 60px;
		box-sizing: border-box;
	}

	.contents_wrap{
		.contents_inner{
			padding: 0 20px;
		}

		.main_menu_wrap{
			position: relative;
			padding-top: 49px;
			box-sizing: border-box;

			.main_menu_list_wrap{
				@include mixins.scrollX;
				border-bottom: 1px solid variables.$colorGray03;
				top: 0;
				left: 0;

				.menu_list{
					white-space: nowrap;

					.menu_list_item{
						display: inline-block;
						padding: 2px 15px 0;
						vertical-align: top;
						box-sizing: border-box;

						.menu_link{
							line-height: 50px;
						}

						&:first-child{
							padding-left: 20px;
						}

						&:last-child{
							padding-right: 20px;
						}
					}
				}
			}
		}
	}
}