@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

/* 통합검색 */
.auto_complete_wrap {
	display: block;
	padding-top: 16px;

	.search_content_wrap {
		display: block;
		box-sizing: border-box;
		padding-top: 4px;

		// 데이터 없는 경우
		.no_data_box {
			display: block;
			box-sizing: border-box;

			.no_data_text {
				display: block;
				position: relative;
				color: variables.$textColorLightGray;
				@include mixins.font_size_xs;
			}
		}

		.related_contents_area {

			& ~ .related_contents_area {
				margin-top: 34px;
			}

			// 상품
			.related_prod_swiper_wrap {
				display: block;
				margin: 0 -20px;

				.related_prod_swiper {
					padding: 0 10px;

					.swiper-slide {
						padding: 0 10px;
						box-sizing: border-box;
					}
				}
			}

			// 이벤트
			.related_event_list_wrap {
				display: block;
				margin: 0 -20px;
				padding: 0 20px;
				@include mixins.scrollX;

				.related_event_list {
					@include mixins.justify_width();
					align-items: stretch;

					.related_event_item {
						flex-shrink: 0;
						width: 200px;

						& ~ .related_event_item {
							margin-left: 16px;
						}

						.related_event_link {
							display: block;
							width: 100%;

							.event_img_box {
								display: block;
								width: 100%;
								overflow: hidden;
								border-radius: 10px;

								img {
									width: 100%;
								}
							}
							.event_info_box {
								display: block;
								margin-top: 12px;

								.event_title {
									display: block;
									color: variables.$textColorBlack;
									@include mixins.font_size_sm;
									font-weight: variables.$fontWeightMedium;
								}
								.event_period {
									display: block;
									margin-top: 5px;
									color: variables.$textColorLightGray;
									@include mixins.font_size_xs;
								}
							}
						}
					}
				}
			}

			// 작가
			.related_author_list_wrap {
				.related_author_list {
					.related_author_item {
						display: block;
						color: variables.$textColorBlack;
						@include mixins.font_size_sm;

						.related_author_link {
							@include mixins.justify_width(start, start, row, true);

							&:hover, &:focus, &:active {
								text-decoration: underline;
							}

							.author {
								flex: 0 0 auto;
								margin-right: 3px;
							}
							.text {
								flex: 1 0 0;
								@include mixins.multi_ellipsis(variables.$fontSizeSmLineHeight, 2, false);
								.matched {
									font-weight: variables.$fontWeightBold;
								}
							}
						}

						& ~ .related_author_item {
							margin-top: 16px;
						}
					}
				}
			}
		}
	}
}