@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';

.wrapper{
	/*&.docker{
		!*padding-bottom: 74px;
		padding-bottom: calc(constant(safe-area-inset-bottom) + 74px);
		padding-bottom: calc(env(safe-area-inset-bottom) + 74px);*!
	}*/

	.footer_wrapper{
		margin-top: 80px;

		.notice_wrap{
			@include mixins.justify_width(center);
			border-top: 1px solid variables.$colorGray03;
			height: 60px;
			padding: 0 20px;
			background-color: variables.$colorGray06;
			box-sizing: border-box;

			.notice_text{
				flex-shrink: 0;
				display: inline-block;
				@include mixins.font_size_sm;
				font-weight: 700;
				vertical-align: top;
			}

			.notice_link{
				overflow: hidden;
				display: inline-block;
				margin-left: 20px;
				@include mixins.ellipsis;
				@include mixins.font_size_sm;
				text-decoration: underline;
				vertical-align: top;
			}
		}

		.footer_body{
			border-top: 1px solid variables.$colorGray03;
			padding: 34px 20px 30px;

			.logo_box{
				display: block;
				width: 110px;
				height: 31px;
				margin-bottom: 22px;

				.logo_link{
					display: block;
					height: 100%;

					img{
						width: 100%;
					}
				}
			}

			.footer_info_box{
				font-size: 0;

				.info_text_multi{
					.info_text {
						display: inline-block;
						margin-top: 0;
						vertical-align: top;
					}
				}

				.info_text{
					display: block;
					margin-top: 3px;
					@include mixins.font_size_xs;
					color: variables.$textColorGray;


					.text_inner{
						margin-right: 8px;
					}

					.btn_footer_link{
						margin-top: -3px;

						@include mixins.respond-to(mobileSm){
							margin-top: 3px;
						}
					}
				}

				.gap{
					font-size: 0;

					&:before{
						content: '';
						display: inline-block;
						width: 1px;
						height: 8px;
						margin: 5px 8px 0;
						background-color: variables.$colorGray03;
						vertical-align: top;
					}
				}
			}

			.btn_footer_link{
				display: inline-block;
				padding: 5px 5px;
				border: 1px solid variables.$colorGray02;
				border-radius: 4px;
				@include mixins.font_size_xxs;
				font-weight: 500;
				line-height: 1;
				vertical-align: top;

				&:after{
					content: '';
					display: inline-block;
					width: 6px;
					height: 6px;
					margin: 2px 0 0 2px;
					@include mixins.ratio_images('../images/arw_footer_link_mok', 'png', 6px, 6px);
					vertical-align: top;
				}
			}

			.footer_menu_box{
				margin-top: 22px;

				.footer_menu_list{
					@include mixins.justify_width;
					flex-wrap: wrap;

					.footer_menu_item{
						width: 50%;

						.footer_menu_link{
							@include mixins.font_size_sm;
							font-weight: 500;
						}

						&.privacy{
							.footer_menu_link{
								font-weight: 700;
							}
						}

						&:nth-child(n+3){
							margin-top: 12px;
						}
					}
				}
			}

			.footer_call_box{
				margin-top: 34px;
				font-size: 0;

				.call_text{
					display: inline-block;
					margin-top: 2px;
					@include mixins.font_size_xs;
					font-weight: variables.$fontWeightMedium;
					vertical-align: top;
				}

				.call_num{
					display: inline-block;
					margin: 0 4px 0 6px;
					font-size: variables.$fontSizeXl;
					font-weight: variables.$fontWeightBold;
					letter-spacing: variables.$letterSpacing;
					color: variables.$textColorBlack;
					vertical-align: top;
					line-height: 1;
				}

				.call_text_sm{
					display: inline-block;
					margin-top: 1px;
					@include mixins.font_size_xxs;
					color: variables.$textColorLightGray;

				}
			}

			.copyright_box{
				margin-top: 38px;
				padding-top: 20px;
				border-top: 1px solid variables.$colorGray03;

				.copyright{
					@include mixins.font_size_xs;
					color: variables.$textColorLightGray;
				}

				.sns_share_box{
					font-size: 0;

					.btn_sns_share{
						display: inline-block;
						width: 32px;
						height: 32px;
						vertical-align: top;

						&.youtube{
							background: {
								position: center;
								repeat: no-repeat;
							}
							@include mixins.ratio_images('../images/btn_footer_sns_youtube_mok', 'png', 32px, 32px);
						}

						&.fb{
							background: {
								position: center;
								repeat: no-repeat;
							}
							@include mixins.ratio_images('../images/btn_footer_sns_fb_mok', 'png', 32px, 32px);
						}

						&.insta{
							background: {
								position: center;
								repeat: no-repeat;
							}
							@include mixins.ratio_images('../images/btn_footer_sns_insta_mok', 'png', 32px, 32px);
						}

						+ .btn_sns_share{
							margin-left: 8px;
						}
					}
				}

			}
		}
	}

	.docker_wrapper{
		position: fixed;
		height: 74px;
		width: 100%;
		bottom: 0;
		left: 0;
		z-index: 100;
		@include mixins.transform(translateY(0));
		@include mixins.transition(transform .23s);

		.docker_list{
			position: relative;
			@include mixins.justify_width;
			padding: 0 12px 12px;
			padding-bottom: calc(constant(safe-area-inset-bottom) + 12px);
			padding-bottom: calc(env(safe-area-inset-bottom) + 12px);

			.docker_item{
				position: relative;
				display: inline-block;
				flex: 1;
				height: 100%;
				z-index: 2;

				[class^=btn_docker]:not(.btn_docker_my){
					display: inline-block;
					width: 100%;
					padding: 16px 0 18px;
					vertical-align: top;
					box-sizing: border-box;

					&:after{
						content: '';
						display: block;
						width: 28px;
						height: 28px;
						margin: 0 auto;
						background-repeat: no-repeat;
						background-size: 100%;
						background-position: 0 0;
					}

					&.btn_docker_home{
						&:after{
							@include mixins.ratio_images('../images/btn_docker_home_mok', 'png', 28px, 28px);
						}
					}

					&.btn_docker_search{
						&:after{
							@include mixins.ratio_images('../images/btn_docker_search_mok', 'png', 28px, 28px);
						}
					}

					&.btn_docker_my{
						&:after{
							@include mixins.ratio_images('../images/btn_docker_my_mok', 'png', 61px, 61px);
						}
					}

					&.btn_docker_history{
						&:after{
							@include mixins.ratio_images('../images/btn_docker_history_mok', 'png', 28px, 28px);
						}
					}

					&.btn_docker_menu{
						&:after{
							@include mixins.ratio_images('../images/btn_docker_menu_mok', 'png', 28px, 28px);
						}
					}
				}

				&.my{
					padding: 0 21px;
					text-align: center;
					z-index: 1;

					.btn_docker_my{
						overflow: hidden;
						position: relative;
						display: inline-block;
						width: 61px;
						height: 61px;
						vertical-align: top;
						border-radius: 100%;
						z-index: 1;
						box-shadow: 0 5px 10px 0 rgba(77, 172, 39, 0.5);

						.img_box{
							display: none;
						}

						&:after{
							content: '';
							display: block;
							width: 100%;
							height: 100%;
							border-radius: 100%;
							@include mixins.ratio_images('../images/btn_docker_my_mok', 'png', 61px, 61px);
						}
					}

					&:after{
						content: '';
						position: absolute;
						top: 0;
						left: 50%;
						display: block;
						width: 136px;
						height: 74px;
						@include mixins.ratio_images('../images/bg_docker_shape_mok', 'png', 136px, 74px);
						@include mixins.transform(translateX(-50%));
					}

					&.login{
						.btn_docker_my{
							box-shadow: 0 5px 10px 0 rgba(155, 160, 168, 0.5);

							&:after{
								content: none;
							}

							.img_box{
								display: block;
								border-radius: 100%;

								img{
									width: 100%;
								}
							}
						}
					}
				}
			}

			&:before, &:after{
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 50%;
				height: 100%;
				background-color: #fff;
				border-top: 1px solid variables.$colorGray02;
				margin-left: -60px;
			}

			&:after{
				left: auto;
				right: 0;
				margin-left: 0;
				margin-right: -60px;
			}
		}

		.btn_go_top{
			position: fixed;
			bottom: 92px;
			@include mixins.transform(translateY(calc(constant(safe-area-inset-bottom) * -1)));
			@include mixins.transform(translateY(calc(env(safe-area-inset-bottom) * -1)));
			right: -18px;
			display: block;
			width: 47px;
			height: 47px;
			border-radius: 100%;
			background-color: #fff;
			border: 1px solid variables.$colorGray03;
			z-index: 100;
			opacity: 0;
			@include mixins.transition(right .23s, opacity .23s);
			box-shadow: 0 5px 10px 0 rgba(variables.$textColorBlack, 0.1);

			&:before{
				content: '';
				display: block;
				width: 13px;
				height: 18px;
				margin: 13px auto 0;
				@include mixins.ratio_images('../images/arw_go_top_mok', 'png', 13px, 18px);
			}

			&.active{
				opacity: 1;
				right: 18px;
			}
		}

		&:not(.docker_fixed){
			&:after{
				content: '';
				position: absolute;
				left: 0;
				bottom: 0;
				width: 100%;
				padding-bottom: calc(constant(safe-area-inset-bottom));
				padding-bottom: calc(env(safe-area-inset-bottom));
				background-color: #fff;
			}
		}

		&.scroll_down{
			@include mixins.transform(translateY(100%));
		}

		&.docker_fixed{
			padding-bottom: calc(constant(safe-area-inset-bottom));
			padding-bottom: calc(env(safe-area-inset-bottom));
			background-color: #fff;

			.btn_go_top{
				/*bottom: 92px;
				bottom: calc(constant(safe-area-inset-bottom) + 92px);
				bottom: calc(env(safe-area-inset-bottom) + 92px);
				!*@include mixins.transform(translateY(calc(constant(safe-area-inset-bottom))));
				@include mixins.transform(translateY(calc(env(safe-area-inset-bottom))));*!*/
			}
		}
	}
}