@use 'sass:map';
@use 'abstracts/variables';
@use 'abstracts/functions';
@use 'abstracts/mixins';

// ios safari 전용 css
@supports (-webkit-touch-callout: inherit) {
	// 뱃지 사이즈
	.badge_sm {
		&:not(.badge_pill) {
			.text {
				transform: translateY(0);
			}
		}
	}

	.wrapper {
		.container_wrapper {
			// 상품상세 상단
			.prod_detail_header {
				.header_contents_inner {
					.prod_detail_view_wrap {

						.prod_detail_info_area {
							.prod_info_wrap {
								.prod_author_box {
									.auto_overflow_footer {
										.btn_more_detail {
											margin-top: 1px;
										}
									}
								}
							}
						}

					}
				}
			}
		}
	}
}